.main-nav {
  position: sticky;
}
@media (max-width: 767px) {
  .main-nav {
    position: relative;
  }
  .main-nav::after {
    display: block;
    position: absolute;
    content: "";
    height: 7px;
    width: 100%;
    margin-top: -7px;
    background-image: linear-gradient(180deg, transparent, rgba(1, 1, 1, 0.2) 100%);
  }
  .main-nav--closed .main-nav__list {
    display: none;
  }
}
.main-nav--nojs button {
  display: none;
}

@media (max-width: 767px) {
  .main-nav__item {
    background-color: #81b3d2;
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2), inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
  .main-nav__item:first-child {
    box-shadow: 0 1px 0 rgba(1, 1, 1, 0.2);
  }
  .main-nav__item:last-child {
    box-shadow: inset 0 1px 0 rgba(255, 255, 255, 0.2);
  }
  .main-nav__item:not(:last-child)::after {
    display: block;
    content: "";
    height: 1px;
    width: 100%;
    background-color: rgba(1, 1, 1, 0.2);
  }
  .main-nav__item--current {
    color: #766357;
  }
}
@media (min-width: 768px) {
  .main-nav__item {
    display: block;
    width: 20%;
    background-color: #ffffff;
    min-height: 56px;
  }
  .main-nav__item:nth-child(4n-2) {
    margin-right: 20%;
  }
}

.main-nav__link {
  display: block;
  font-weight: 700;
  font-size: 18px;
  line-height: 40px;
  text-align: center;
  border: none;
  padding-top: 10px;
  padding-bottom: 5px;
}
@media (max-width: 767px) {
  .main-nav__link {
    color: #ffffff;
  }
  .main-nav__link:hover, .main-nav__link:focus {
    background-color: #669ec0;
  }
  .main-nav__link:active {
    background-color: #5496bd;
    color: rgba(255, 255, 255, 0.3);
  }
}
@media (min-width: 768px) {
  .main-nav__link {
    font-size: 14px;
    line-height: 26px;
    padding-top: 16px;
    color: #000000;
  }
  .main-nav__link:hover {
    color: #604e43;
  }
  .main-nav__link:active {
    opacity: 0.3;
  }
  .main-nav__link--current {
    color: #766357;
  }
}

@media (min-width: 768px) {
  .main-nav__list {
    display: flex;
    justify-content: stretch;
  }
}

.main-nav__button {
  position: absolute;
  width: 22px;
  height: 22px;
  border: none;
}
.main-nav__button--open {
  width: 22px;
  height: 22px;
  top: 17px;
  right: 21px;
  background-color: #81b3d2;
  cursor: pointer;
}
.main-nav__button--open:hover {
  opacity: 0.6;
}
.main-nav__button--open:active {
  opacity: 0.3;
}
.main-nav__button--open::before, .main-nav__button--open::after {
  display: block;
  content: " ";
  background-color: #ffffff;
  width: 22px;
  height: 5px;
  position: absolute;
  margin: 0;
}
.main-nav__button--open::before {
  top: 4px;
  left: 0;
}
.main-nav__button--open::after {
  bottom: 4px;
  left: 0;
}
@media (min-width: 768px) {
  .main-nav__button--open {
    display: none;
  }
}
.main-nav__button--close {
  background-color: transparent;
  position: absolute;
  cursor: pointer;
  top: 17px;
  right: 30px;
}
.main-nav__button--close:hover {
  opacity: 0.6;
}
.main-nav__button--close:active {
  opacity: 0.3;
}
.main-nav__button--close::before, .main-nav__button--close::after {
  display: block;
  content: " ";
  background-color: #ffffff;
  width: 28px;
  height: 4px;
}
.main-nav__button--close::before {
  transform: translateY(2px) rotate(-45deg);
}
.main-nav__button--close::after {
  transform: translateY(-2px) rotate(45deg);
}
@media (min-width: 768px) {
  .main-nav__button {
    display: none;
  }
}

